@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');
$gym: url(../../Images/gym-bg.png);
$scoop: url(../../Images/whey-scoop-bg.png);

.entire-page {
  display: flex;
  flex-direction: column;
}

.main-view {
  @media (max-width: 768px) {
    align-items: center;
  }
  height: 100.1vh;
  display: flex;
  flex-direction: column;
  background-image: $gym;
  background-size: cover;
}

.main-welcome {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: inherit;
  margin: 0 8rem;
}

.main-curl {
  @media (max-width: 768px) {
    visibility: hidden;
  }
  position: absolute;
  z-index: 1;
  height: 80%;
  left: -200px;
  bottom: 0;
  opacity: 0.7;
}

.main-welcome-title {
  @media (max-width: 768px) {
    font-size: 5rem;
    text-align: center;
  }
  font-family: 'Russo One', sans-serif;
  font-size: 9rem;
  color: #8b1313;
  text-shadow: 0 0 50px #000;
  z-index: 2;

  span {
    font-family: 'Russo One', sans-serif;
    color: #fff;
    text-shadow: 0 0 50px #000;
  }
}

.main-welcome-text {
  @media (max-width: 768px) {
    font-size: 1.5rem;
    width: 95%;
  }
  text-align: center;
  font-weight: 900;
  color: #fff;
  text-shadow: 2px 2px 5px #000;
  font-size: 2rem;
  width: 70%;
  z-index: 2;
}

.main-calculator {
  min-height: 92vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: $scoop;
  background-size: cover;
  background-position: center;
}

.main-forms {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.forms-wrap {
  @media (max-width: 768px) {
    flex-direction: column;
  }
  display: flex;
  flex-direction: row;
  align-items: center;
}

.compare-button {
  @media (max-width: 768px) {
    margin: 1rem 0;
  }
  font-family: 'Russo One', sans-serif;
  width: 8rem;
  height: 3rem;
  border-radius: 20px;
  font-size: x-large;
  border: 2px solid #8b1313;
  color: #8b1313;
  background-color: #000;
  transition: 300ms;

  &:hover {
    color: #fff;
    border: 2px solid #bf1515;
    background-color: #bf1515;
    transition: 300ms;
  }
}

.main-forms-button {
  display: flex;
  align-items: center;
  justify-content: center;
}